@import url(https://fonts.googleapis.com/css2?family=Fira+Code:wght@300;400;500;600;700&family=Ubuntu:wght@300;400;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

html {
    overflow: scroll;
    overflow-x: hidden;
}
::-webkit-scrollbar {
    width: 0; 
    background: transparent; 
}

body{
    background-color: rgb(8, 19, 3);
}

nav{
    /* border: 1px solid red;   */
    height: 10vh;
    width: 80%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 0 100px 0;
    font-family: 'Ubuntu', sans-serif;
    color: rgb(218, 218, 218);
}

h2{
  margin: 0% 3% ;
  cursor:default;
}
h2:hover{
    color: rgb(194, 193, 193);
    
}


#btn-record{
    margin-right: 5%;
    padding: 8px 14px;
    font-size: medium;
    font-weight: 900;
    color: #fff;
    background-color: rgb(0, 141, 7);
    border: none;
    border-radius: 5px;
    letter-spacing: 0.5px;
    cursor: pointer;
}
#btn-record:hover{
    background-color: rgba(4, 255, 17, 0.651);
}
#container1{
    /* border: 1px solid green; */
    display: block;
    margin: 0.5% auto;
    width: 60%;
    height: 85vh;
    border-radius: 2%;
    background-color: black;
}

#Monoco{
    width: 98%;
    margin: 0% auto;
}

#btn-div{
    display: flex;
    justify-content: right;
    align-items: center;
}

#select-div{
    width: 16vw;
    display: flex;
    align-items: center;
    margin: 5px 0px;
    color: #fff;
}


   

#select-div label{
    font-size: larger;
    font-weight: bolder;
    margin-right: 5px ;
}


@media (max-width : 800px) {
    #Monoco{
        width: 90%;
        margin: 0% auto;
    }
}
@media (max-width : 500px) {
    #Monoco{
        width: 100%;
        margin: 0% auto;
    }
    nav{
        font-size: 200%;
    }
}
*{
    box-sizing: border-box;
    margin: 0%;
    padding: 0%;
}

#box{
    border: 1px solid black;
    width: 100%;
    height: 100vh;
    background-color: rgb(0, 0, 0);
}
#back{
    width: 40px;
    height: 40px;
    -webkit-filter: saturate(10000);
            filter: saturate(10000);
}

/* body{
    overflow: hidden;
} */
#record{

     position: absolute;
     left: 15%;
     bottom: 10%;
     /* border: 1px solid red; */
     width: 70%;
     margin: 0 auto;
     height: 85vh;
     display: flex;
     align-items: center;
     background: linear-gradient(#00ccffd0,#d400d4ce);
     z-index: 3;
}


#container{
    /* border: 1px solid yellow; */
    display: block;
    height: 85%;
    width: 85%;
    border-radius: 20px;
    position: relative;
    left: 4%;
    margin: 0%;
    background-color: rgb(30 30 30);
}

#Monoco{
    /* border: 1px solid blue; */
    width: 87.5%;
    margin: 1% 2% ;
}

#vid{
    /* border: 1px solid greenyellow; */
    width: 170px;
    margin: 0%;
    height: 50vh;
    position: absolute;
    right: 2%;
    border-radius: 20px;
}

#download-vid{
    overflow: hidden;
    display: block;
    /* border: 1px solid #fff; */
    width: 60%;
    height: 70vh;
    margin: 5% auto 0%;
    opacity: 0;
}

#download-btn{
    display: block;
    padding: .5rem 1rem;
    outline: none;
    border: none;
    font-weight: 900;
    letter-spacing: 1px;
    border-radius: 5px;
    margin: 3% auto;
    background-color: rgba(0, 204, 0, 0.884);
    color: #fff;
    font-size: larger;
    cursor: pointer;
}
#download-btn:hover{
    background-color: rgb(255, 175, 1);;
}

#btnStart{
    border: none;
    outline: none;
    display: inline-block;
    margin: 0% auto;
    padding: 0.5%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: larger;
    font-weight: lighter;
    border-radius: 5px;
    background-color: red;
    color: #fff;
    position: absolute;
    top: 92%;
    left: 45%;
}

#btnStart:hover{
    cursor: pointer;
    background-color: rgb(255, 72, 0);
}

img{
    /* border: 1px solid red; */
    width: 20px;
    height: 20px;
    margin: 0px 5px;
}

#divStop{
    /* border: 1px solid red; */
    width: 15%;
    height: 6vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: black;
    margin: 0% auto;
    z-index: 0;
    position: absolute;
    top: 92%;
    left: 43.5%;
}

#btnStop{
    border: none;
    outline: none;
    margin: 0% auto 0;
    padding: 4%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: larger;
    font-weight: lighter;
    border-radius: 5px;
    background-color: #4c2778;
    color: #fff;
    cursor: pointer;
    -webkit-text-decoration-line: none!important;
            text-decoration-line: none!important;
    text-align: center;
    width: 100px;
    height: 28px;
}

canvas{ 
    display: block;
    width: 60%; 
    height: 70vh; 
    /* border: 1px solid yellow; */
    margin: 2% auto 0;
}

#canva{
    background-color: aqua;
    position: relative;
}

#cropCvs{
    /* border: 1px solid red; */
    position: absolute;
    top: 40px;
    left: 275px;
}

#play-button{
    font-size: 200%;
    width: 50px;
    height: 50px;
    text-align: center;
    border-radius: 100%;
    padding: 7px;
    background-color: rgb(0, 0, 0);
    color: #fff;
    outline: none;
    border: none;
    position: absolute;
    top: 40%;
    left: 47%;
    cursor: pointer;
    z-index: 2;
    opacity: 0.5;
}

#success{
    border: 1px solid red;
    width: 640px;
    height: 360px;
    position: absolute;
    top: 13%;
    left: 30%;
    z-index: -99;

}
